@charset "UTF-8";
@import "../vendors/bootstrap/variables";

// nieco ciemniszy kolor linkow (w sytuacji gdy wiele linkow jest obok siebie domyslny kolor jest zbyt jasny)
$link-color-darker: darken($brand-primary, 10%);

// alternatywny kolor linkow uzywany po naprowadzeniu kursora myszy nad link
// uzywany w niewielu sytuacjach w kontrascie do koloru zielonego dominujacego w serwisie
$alt-link-hover-color: #d7661c;

// obramowanie elementow typu "boksy" (tabele, div itp)
$border-color: #d0d0d0;

// kolor naglowka <h4> znajdujacego sie w elemencie .box (elementy sekcji)
$box-header-color: #646466;
// kolor border-bottom dla elementu <h4> znajdujacego sie w elemencie .box
$box-border-color: #c7c7c7;